  const getPromoDiscount = async (e) => {
    e.preventDefault();

    let inputVal = document.getElementById("promoCodeField").value;

    if (inputVal === "") {
      setGlobalDiscount({ amount: 0, description: "" });
      return;
    }

    let discountFields = {
      securityToken: state.securityToken,
      method: "1",
      promo: inputVal,
    };

    // discountFields = JSON.stringify(discountFields)

    try {
      const res = await axios.post(
        API + "api/v1/resources/promos/?",
        discountFields,
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      );

      if (res.data !== null) {
        let errorCode = "";
        if (res.data.Errors) {
          errorCode = "Invalid Promo Code";
          setGlobalDiscount({
            amount: 0,
            description: "",
            promoID: "",
            promoCode: "",
            error: errorCode,
          });
        } else {
          let valid = true;
          if (parseFloat(res.data.Promo[0].promoAmountIfMoreThen))
            if (
              parseFloat(getTotalPrice() * 1.0) <
              parseFloat(res.data.Promo[0].promoAmountIfMoreThen)
            ){
              valid = false;
              errorCode = "Minimum Order Amount Not Met"
            }
          if (parseInt(res.data.Promo[0].promoStart)) {
            if (
              parseInt(res.data.dateNow) <
              parseInt(res.data.Promo[0].promoStart)
            ){
              valid = false;
              errorCode = "Invalid Promo Code"
            }
          }
          if (parseInt(res.data.Promo[0].promoEnd)) {
            if (
              parseInt(res.data.dateNow) > parseInt(res.data.Promo[0].promoEnd)
            ){
              valid = false;
              errorCode = "Promo Code Has Expired"
            }
          }
          valid
            ? setGlobalDiscount({
                amount: res.data.Promo[0].promoDiscount,
                description: res.data.Promo[0].promoDescription,
                promoID: res.data.Promo[0].promoID,
                promoCode: res.data.Promo[0].promoCode,
                error: "",
              })
            : setGlobalDiscount({
                amount: 0,
                description: "",
                promoID: "",
                promoCode: "",
                error: errorCode,
              });
        }
      }
    } catch (err) {
      console.log("Error getPromoDiscount  >> ", err);
    }
  };

