import React from 'react';
import ReactDOM from 'react-dom';
import FormProvider from './context/FormContext'
import './index.css';
import App from './App';
import axios from "axios";

const createNewJob = async () => {
    let clientID = 987;

    let newJobFields = {
                        securityToken: "37980",
                        method: 3,
                        clientID: parseInt(clientID),
                        Fields: [
                          {
                            fieldID: 978,
                            fieldValue: 'Online booking. No summary data'
                          },
                          {
                            fieldID: 968,
                            fieldValue: "ff20",
                          },
                          {
                            fieldID: 982,
                            optionID: 10278
                          } 
                        ]
                      }

    // newJobFields = JSON.stringify(newJobFields)

    try {
      const res = await axios.post('https://kingtote.vonigo.com/api/v1/data/Jobs/', newJobFields, {headers: {
        'Content-Type': 'application/json',
        }
    });
      if(res.data.errNo === 0){
        return res.data.Job.objectID;
      }
      else{
        return null
      }
    } catch (err) {
      console.log('Error createNewJob >> ', err)
      return null;
    }
  }

//   createNewJob();

ReactDOM.render(
    <FormProvider>
        <App />
    </FormProvider>
, document.getElementById('root'));
